@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;600;700;800;900&display=swap);
.navbar-component{background:white;padding:20px 30px;position:obsolute;top:0px;right:0px;left:0px;width:100%;background:none}.navbar-component .top-logo{font-size:1.2rem;display:inline-block;font-weight:900;font-family:'Nunito Sans', 'Avenir Next', -apple-system, sans-serif}

.jumbo-component{width:100%;height:100vh;width:100vw;background-color:#ffffff;background-image:url("https://www.transparenttextures.com/patterns/inspiration-geometry.png")}.jumbo-component .intro-container{text-align:center;display:block;padding:30vh 20px}.jumbo-component .intro-container .big-title{color:#1f1f1f;font-size:6rem;text-transform:uppercase;font-weight:900;margin-bottom:-10px}.jumbo-component .intro-container .sub-title{color:#aaa9ad;text-transform:capitalize;font-size:1.8rem;font-weight:700}.jumbo-component .intro-container .sub-sub-title{padding-top:20px;font-size:1.4rem;opacity:0.5;display:inline-block;max-width:600px}@media (max-width: 760px){.jumbo-component .intro-container .big-title{font-size:4rem}.jumbo-component .intro-container .sub-title{font-size:1.3rem}}

.portfolio-component .demo-component{padding:30px}.portfolio-component .demo-component .intro-col{padding-top:200px}@media (max-width: 760px){.portfolio-component .demo-component .intro-col{padding-top:0px}}.portfolio-component .demo-component .intro-col .project-title{font-weight:900;font-size:3rem;text-transform:uppercase}.portfolio-component .demo-component .intro-col .project-point .content{font-size:1.4rem;opacity:0.5;font-weight:700}.portfolio-component .demo-component .intro-col .action-container{padding-top:20px}.portfolio-component .demo-component .intro-col .action-container .button{padding:15px 30px;background:#B99458;color:white;font-weight:800;display:inline-block;border-radius:100px;text-transform:uppercase;letter-spacing:1px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none}.portfolio-component .demo-component .intro-col .action-container .button:hover{background:#c8ab7c;transition:all 0.1s;transform:scale(0.98)}.portfolio-component .demo-component .intro-col .action-container .button:active{background:#c8ab7c;transform:scale(0.95)}@media (max-width: 760px){.portfolio-component .demo-component .image-col img{transform:translateX(-50px);height:500px}}

.service-component.container{padding-bottom:100px}.service-component.container .stack-component{margin-top:50px;position:relative}.service-component.container .stack-component .col-sm-12.col-md-6.col-lg-3{padding-left:10px;padding-right:10px}.service-component.container .stack-component .item{padding:10px 20px;text-align:center;background:white;box-shadow:0 0 50px rgba(0,0,0,0.1);border-radius:20px;margin-bottom:50px;height:360px;margin-top:30px}@media (max-width: 1250px){.service-component.container .stack-component .item{height:400px}}.service-component.container .stack-component .item .info-container{transform:translateY(-30px)}.service-component.container .stack-component .item .info-container img{margin-top:5px;max-height:64px;max-width:300px}.service-component.container .stack-component .item .title{font-weight:800;font-size:1.6rem;color:rgba(0,0,0,0.8)}.service-component.container .stack-component .item .details{padding-top:10px;color:#878787;font-weight:700}.service-component.container .stack-component .item .icon-card{transform:translateY(-60px);background:white;box-shadow:0px 8px 20px -2px rgba(0,0,0,0.1);display:inline-block;padding:30px;border-radius:300px;color:white;background:#B99458;height:101px;width:101px}.service-component.container .stack-component .item .icon-card i.fa{font-size:2.5rem}@media (max-width: 760px){.service-component.container .stack-component .item{padding-left:30px;padding-right:30px;height:400px}.service-component.container .stack-component .item .title{font-size:2rem}.service-component.container .stack-component .item .details{font-size:1.3rem}.service-component.container .stack-component .item .info-container img{max-height:80px}}.service-component.container .stack-component .item:hover .title,.service-component.container .stack-component .item:active .title{color:#000}.service-component.container .stack-component .item:hover .details,.service-component.container .stack-component .item:active .details{color:#787878}.service-component.container .stack-component .item:hover .icon-card,.service-component.container .stack-component .item:active .icon-card{transition:.2s size}

.value-component{padding:50px 0px;background:rgba(31,31,31,0.03)}.value-component .item-component{margin-top:50px;position:relative}.value-component .item-component .col-xs-12{padding-left:3px;padding-right:3px}.value-component .item-component .item{padding:10px;text-align:center;border-radius:10px;margin-bottom:50px;margin-top:30px;height:200px}.value-component .item-component .item .info-container{transform:translateY(-30px)}.value-component .item-component .item .info-container img{margin-top:5px;max-height:64px;max-width:300px}.value-component .item-component .item .title{font-weight:800;font-size:1.3rem;color:#1f1f1f}.value-component .item-component .item .details{font-size:1rem;padding-top:10px;color:#aaa9ad;font-weight:700}.value-component .item-component .item .icon-card{transform:translateY(-50px);background:white;box-shadow:0px 8px 20px -2px rgba(0,0,0,0.1);display:inline-block;padding:18px;height:70px;width:70px;border-radius:20px;color:#B99458}.value-component .item-component .item .icon-card i.fa{font-size:2rem}@media (max-width: 760px){.value-component .item-component .item{padding-left:30px;padding-right:30px;height:200px;margin-top:80px}.value-component .item-component .item .title{font-size:2rem}.value-component .item-component .item .details{font-size:1.3rem}.value-component .item-component .item .info-container img{max-height:80px}}

.contact-component{padding:50px 0px 100px;background-color:#ffffff;background-image:url("https://www.transparenttextures.com/patterns/inspiration-geometry.png")}.contact-component .contact-form{background:white;box-shadow:0 0 50px rgba(0,0,0,0.1);border-radius:20px;padding:50px}.contact-component .contact-form .label{padding:0px 0px 5px 0px;font-size:1.2rem;font-weight:800}.contact-component .contact-form .margin-top{margin-top:20px}.contact-component .contact-form input,.contact-component .contact-form textarea{outline:none;border-radius:5px;border:4px solid rgba(170,169,173,0.2);width:100%;padding:15px 20px;font-size:1.4rem;font-weight:700;border-radius:10px}.contact-component .contact-form input:active,.contact-component .contact-form input:hover,.contact-component .contact-form textarea:active,.contact-component .contact-form textarea:hover{border:4px solid rgba(170,169,173,0.5)}.contact-component .contact-form input:focus,.contact-component .contact-form textarea:focus{box-shadow:0 0 20px rgba(0,0,0,0.1);border:4px solid rgba(185,148,88,0.7)}.contact-component .contact-form .action{padding-top:20px;text-align:right}.contact-component .contact-form .button{padding:15px 30px;background:#B99458;color:white;font-weight:800;display:inline-block;border-radius:100px;text-transform:uppercase;letter-spacing:1px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none}.contact-component .contact-form .button:hover{background:#c8ab7c;transition:all 0.1s;transform:scale(0.98)}.contact-component .contact-form .button:active{background:#c8ab7c;transform:scale(0.95)}@media (max-width: 760px){.contact-component .contact-form{padding:20px}}

.bottom-component{padding:30px;background:#B99458;text-align:center;color:white}.bottom-component .item-component{margin:auto;display:flex;justify-content:center;align-items:center}.bottom-component .item-component div{opacity:0.8;padding:0px 20px}.bottom-component .item-component div:hover{opacity:1}.bottom-component .item-component .logo{font-weight:900}.bottom-component .item-component .link{font-size:1.7rem}.bottom-component .item-component .link,.bottom-component .item-component .email{cursor:pointer}.bottom-component .item-component .link a,.bottom-component .item-component .email a{color:white}@media (max-width: 760px){.bottom-component .item-component{flex-direction:column}.bottom-component .item-component div{font-size:1.3rem;padding:10px}.bottom-component .item-component .link{font-size:2.5rem}}@media (max-width: 760px){.bottom-component{background:white;padding-bottom:100px;border-bottom:20px solid #B99458}.bottom-component .item-component{color:#1f1f1f}.bottom-component .item-component .link a,.bottom-component .item-component .email a{color:#1f1f1f}}

.landing-component{overflow-x:hidden}.landing-component .landing-big-title{text-transform:uppercase;font-size:6rem;font-weight:900;letter-spacing:5px;padding:0px;padding-top:50px;transform:translateX(-7px)}@media (max-width: 760px){.landing-component .landing-big-title{font-size:3rem;letter-spacing:1px}}

*,body{margin:0;font-family:'Nunito Sans', 'Avenir Next', -apple-system, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

